<template>
    <div class="data-empty">
        <div class="img">
        <img src="../../assets/images/empty.png">
        </div>
        <div class="text">
        <span>暂无数据...</span>
        </div>          
    </div>
</template>

<script>
export default {
  data() {
    return {};
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

.data-empty {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  .img {
    @include wh(200px,200px);
    img {
      @include wh(200px, 200px);
    }
  }
  .text {
    @include fc(14px, #9b9b9b);
    width: 200px;
    text-align: center;
    margin-top: 20px;
  }
}
</style>




